.knowladge-item {
    flex-basis: 40%;
    position: relative;
    display: flex;
    @media (max-width: 575px) {
        flex-basis: 50%;
    }

    &__icon {
        width: 100px;
        height: 100px;
        position: relative;
        margin: 10px auto;

        background-color: transparent;

        padding-top: 10px;

        &::before {
            content: "";
            display: block;
            border: 1px solid black;
            box-shadow: 0px 0px 16px -4px black;
            width: 100%;
            height: 100px;
            top: 0;
            position: absolute;
            transition: 0.4s;
        }

        img {
            z-index: 2;
            width: 85%;
            padding: 5px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition: 0.5s;
        }
    }
}
